:global .loading_wrap {
  text-align: center;
  padding: 100px 0;
}
:global .gutter-box {
  margin-top: 36px;
}
:global .gutter-box:nth-child(odd) {
  padding-left: 36px;
}
.articleItem_box {
  width: 455px;
  height: 455px;
  background-color: rgba(0, 0, 0, .45);
  cursor: pointer;
  position: relative;
}
.articleItem_box > img {
  width: 100%;
  height: 100%;
}
.articleItem_box .artInfo_bottom {
  position: absolute;
  bottom: 0;
}
.articleItem_box .artInfo_bottom h1,
.articleItem_box .artInfo_bottom span,
.articleItem_box .artInfo_bottom div {
  color: #fff;
}
.articleItem_box .artInfo_bottom h1 {
  font-size: 24px;
  padding: 0 15px;
}
.articleItem_box .artInfo_bottom h1:after {
  content: '';
  display: block;
  height: 0;
  width: 60%;
  border-bottom: 1px solid #fff;
  padding-top: 20px;
}
.articleItem_box .artInfo_bottom > div {
  padding: 5px 15px;
}



