@import '~highlight.js/styles/atom-one-dark.css';
.article-detail {
  font-family: Helvetica, arial, sans-serif;
  font-size: 14px;
  line-height: 1.6;
  color: #333;
}
.article-detail .article-detail > *:first-child {
  margin-top: 0 !important;
}
.article-detail .article-detail > *:last-child {
  margin-bottom: 0 !important;
}
.article-detail a {
  color: #4183C4;
}
.article-detail a.absent {
  color: #cc0000;
}
.article-detail a.anchor {
  display: block;
  padding-left: 30px;
  margin-left: -30px;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
}
.article-detail h1,
.article-detail h2,
.article-detail h3,
.article-detail h4,
.article-detail h5,
.article-detail h6 {
  margin: 20px 0 10px;
  padding: 0;
  font-weight: bold;
  -webkit-font-smoothing: antialiased;
  cursor: text;
  position: relative;
}
.article-detail h1:hover a.anchor,
.article-detail h2:hover a.anchor,
.article-detail h3:hover a.anchor,
.article-detail h4:hover a.anchor,
.article-detail h5:hover a.anchor,
.article-detail h6:hover a.anchor {
  background: url(data:image/png;base64,) no-repeat 10px center;
  text-decoration: none;
}
.article-detail h1 tt,
.article-detail h1 code {
  font-size: inherit;
}
.article-detail h2 tt,
.article-detail h2 code {
  font-size: inherit;
}
.article-detail h3 tt,
.article-detail h3 code {
  font-size: inherit;
}
.article-detail h4 tt,
.article-detail h4 code {
  font-size: inherit;
}
.article-detail h5 tt,
.article-detail h5 code {
  font-size: inherit;
}
.article-detail h6 tt,
.article-detail h6 code {
  font-size: inherit;
}
.article-detail h1 {
  font-size: 28px;
  color: black;
}
.article-detail h2 {
  font-size: 24px;
  border-bottom: 1px solid #cccccc;
  color: black;
}
.article-detail h3 {
  font-size: 18px;
}
.article-detail h4 {
  font-size: 16px;
}
.article-detail h5 {
  font-size: 14px;
}
.article-detail h6 {
  color: #777777;
  font-size: 14px;
}
.article-detail p,
.article-detail blockquote,
.article-detail ul,
.article-detail ol,
.article-detail dl,
.article-detail li,
.article-detail table,
.article-detail pre {
  margin: 15px 0;
}
.article-detail hr {
  background: transparent url(data:image/png;base64,) repeat-x 0 0;
  border: 0 none;
  color: #cccccc;
  height: 4px;
  padding: 0;
}
.article-detail body > h2:first-child {
  margin-top: 0;
  padding-top: 0;
}
.article-detail body > h1:first-child {
  margin-top: 0;
  padding-top: 0;
}
.article-detail body > h1:first-child + h2 {
  margin-top: 0;
  padding-top: 0;
}
.article-detail body > h3:first-child,
.article-detail body > h4:first-child,
.article-detail body > h5:first-child,
.article-detail body > h6:first-child {
  margin-top: 0;
  padding-top: 0;
}
.article-detail a:first-child h1,
.article-detail a:first-child h2,
.article-detail a:first-child h3,
.article-detail a:first-child h4,
.article-detail a:first-child h5,
.article-detail a:first-child h6 {
  margin-top: 0;
  padding-top: 0;
}
.article-detail h1 p,
.article-detail h2 p,
.article-detail h3 p,
.article-detail h4 p,
.article-detail h5 p,
.article-detail h6 p {
  margin-top: 0;
}
.article-detail li p.first {
  display: inline-block;
}
.article-detail li {
  margin: 0;
}
.article-detail ul,
.article-detail ol {
  padding-left: 30px;
}
.article-detail ul :first-child,
.article-detail ol :first-child {
  margin-top: 0;
}
.article-detail dl {
  padding: 0;
}
.article-detail dl dt {
  font-size: 14px;
  font-weight: bold;
  font-style: italic;
  padding: 0;
  margin: 15px 0 5px;
}
.article-detail dl dt:first-child {
  padding: 0;
}
.article-detail dl dt > :first-child {
  margin-top: 0;
}
.article-detail dl dt > :last-child {
  margin-bottom: 0;
}
.article-detail dl dd {
  margin: 0 0 15px;
  padding: 0 15px;
}
.article-detail dl dd > :first-child {
  margin-top: 0;
}
.article-detail dl dd > :last-child {
  margin-bottom: 0;
}
.article-detail blockquote {
  border-left: 4px solid #dddddd;
  padding: 0 15px;
  color: #777777;
}
.article-detail blockquote > :first-child {
  margin-top: 0;
}
.article-detail blockquote > :last-child {
  margin-bottom: 0;
}
.article-detail table {
  padding: 0;
  border-collapse: collapse;
}
.article-detail table tr {
  border-top: 1px solid #cccccc;
  background-color: white;
  margin: 0;
  padding: 0;
}
.article-detail table tr:nth-child(2n) {
  background-color: #f8f8f8;
}
.article-detail table tr th {
  font-weight: bold;
  border: 1px solid #cccccc;
  margin: 0;
  padding: 6px 13px;
}
.article-detail table tr td {
  border: 1px solid #cccccc;
  margin: 0;
  padding: 6px 13px;
}
.article-detail table tr th :first-child,
.article-detail table tr td :first-child {
  margin-top: 0;
}
.article-detail table tr th :last-child,
.article-detail table tr td :last-child {
  margin-bottom: 0;
}
.article-detail img {
  max-width: 100%;
}
.article-detail span.frame {
  display: block;
  overflow: hidden;
}
.article-detail span.frame > span {
  border: 1px solid #dddddd;
  display: block;
  float: left;
  overflow: hidden;
  margin: 13px 0 0;
  padding: 7px;
  width: auto;
}
.article-detail span.frame span img {
  display: block;
  float: left;
}
.article-detail span.frame span span {
  clear: both;
  color: #333333;
  display: block;
  padding: 5px 0 0;
}
.article-detail span.align-center {
  display: block;
  overflow: hidden;
  clear: both;
}
.article-detail span.align-center > span {
  display: block;
  overflow: hidden;
  margin: 13px auto 0;
  text-align: center;
}
.article-detail span.align-center span img {
  margin: 0 auto;
  text-align: center;
}
.article-detail span.align-right {
  display: block;
  overflow: hidden;
  clear: both;
}
.article-detail span.align-right > span {
  display: block;
  overflow: hidden;
  margin: 13px 0 0;
  text-align: right;
}
.article-detail span.align-right span img {
  margin: 0;
  text-align: right;
}
.article-detail span.float-left {
  display: block;
  margin-right: 13px;
  overflow: hidden;
  float: left;
}
.article-detail span.float-left span {
  margin: 13px 0 0;
}
.article-detail span.float-right {
  display: block;
  margin-left: 13px;
  overflow: hidden;
  float: right;
}
.article-detail span.float-right > span {
  display: block;
  overflow: hidden;
  margin: 13px auto 0;
  text-align: right;
}
.article-detail code,
.article-detail tt {
  margin: 0 2px;
  padding: 0 5px;
  white-space: nowrap;
  border: 1px solid #eaeaea;
  background-color: #f8f8f8;
  border-radius: 3px;
}
.article-detail pre code {
  margin: 0;
  padding: 0;
  white-space: pre;
  border: none;
  background: transparent;
}
.article-detail .highlight pre {
  background-color: #f8f8f8;
  border: 1px solid #cccccc;
  font-size: 13px;
  line-height: 19px;
  overflow: auto;
  padding: 6px 10px;
  border-radius: 3px;
}
.article-detail pre {
  background-color: #f8f8f8;
  border: 1px solid #cccccc;
  font-size: 13px;
  line-height: 19px;
  overflow: auto;
  padding: 6px 10px;
  border-radius: 3px;
}
.article-detail pre code,
.article-detail pre tt {
  background-color: transparent;
  border: none;
}
.article-detail sup {
  font-size: 0.83em;
  vertical-align: super;
  line-height: 0;
}
.article-detail kbd {
  display: inline-block;
  padding: 3px 5px;
  font-size: 11px;
  line-height: 10px;
  color: #555;
  vertical-align: middle;
  background-color: #fcfcfc;
  border: solid 1px #ccc;
  border-bottom-color: #bbb;
  border-radius: 3px;
  -webkit-box-shadow: inset 0 -1px 0 #bbb;
          box-shadow: inset 0 -1px 0 #bbb;
}
.article-detail * {
  -webkit-print-color-adjust: exact;
}
@media screen and (min-width: 914px) {
  .article-detail body {
    width: 854px;
    margin: 0 auto;
  }
}
@media print {
  .article-detail table,
  .article-detail pre {
    page-break-inside: avoid;
  }
  .article-detail pre {
    word-wrap: break-word;
  }
}
.article-detail ul > li {
  list-style-type: disc;
}



